@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
body{
  background-color:white;

}
.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  width: 100%;
}


.wrapper {
  padding-top: 50px;
  background:white;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

h2{
    font-family: 'Fjalla One', sans-serif;
    font-size: 4em;
}
.bodyz{
    font-family: 'Fjalla One', sans-serif;
    font-size: 3em;


}
.jumbotron{
background-color: white;

}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

